export default {
	data() {
		return {
			obs:''
		}
	},
	onLoad(){
		// this.global.utils.sethead("登录")
	},
	methods: {
		userinfor(){
			let that = this;
			uni.login({
			  provider: 'weixin',
			  success: function (loginRes) {
				uni.getUserInfo({
					provider:'weixin',
					success:(userinfor)=>{
						that.obs = JSON.stringify(userinfor) 
						uni.setStorageSync("userinfo",userinfor)
						that.global.request.post({
							url: 'weChat',
							method:"GET",
							data: {
								userinfo:userinfor,
								code:loginRes.code
							},
							isLoading: true,
							success: (res) => {
								that.global.status.state.login=1;
								uni.setStorageSync("session",res);
								uni.navigateBack({
									delta:1
								})
							}
						})	
					}
				})
			  }
			});
		}
		
		
		
	
	}
}
